#skill{position:relative;top:0}.skillsWrapper{margin:0;padding:10vh 0 10vh 0;width:100%;height:100%;display:flex;align-items:center;justify-content:center;perspective:350px;perspective-origin:50% 50%;color:white}.skillsmain{justify-content:center;align-items:center;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr}.skillscontainer{width:150px;height:150px;margin:20px;transform-style:flat}@media screen and (max-width: 960px){.skillscontainer{width:80px;height:80px;margin:5px;color:white}}.skillscontainer>div{position:absolute;width:150px;height:150px;background:grey;display:flex;align-items:center;justify-content:center;overflow:hidden;will-change:transform;cursor:pointer}@media screen and (max-width: 960px){.skillscontainer>div{width:80px;height:80px;margin:auto}}#Skills_title{position:relative;left:38%}

#contact {
  position: relative;
  top: 0;
  margin-bottom: 20vh;
}
.contact-section h1 {
  text-align: center;
  color: #ddd;
}
.border {
  width: 100px;
  height: 10px;
  background: #34495e;
  margin: 40px auto;
}

.contact-form {
  max-width: 600px;
  margin: auto;
  padding: 0 10px;
  overflow: hidden;
}

.contact-form-text {
  display: block;
  width: 100%;
  box-sizing: border-box;
  margin: 16px 0;
  border: 0;
  background: #111;
  padding: 20px 40px;
  outline: none;
  color: #ddd;
  transition: 0.5s;
}
.contact-form-text:focus {
  box-shadow: 0 0 10px 4px #34495e;
}
textarea.contact-form-text {
  resize: none;
  height: 120px;
}
.contact-form-btn {
  float: right;
  border: 0;
  background: #34495e;
  color: #fff;
  padding: 12px 50px;
  border-radius: 20px;
  cursor: pointer;
  transition: 0.5s;
}
.contact-form-btn:hover {
  background: #2980b9;
}

